.list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.item {
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    padding: 30px;
}

.item:nth-child(odd) {
    background-color: #f7f7f7;
}

.item:nth-child(even) {
    background-color: #e8e8e8;
}
